body {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  min-height: 100vh;
}

h1 {
  color: #face7c;
  background-color: #b77cbd;
}

.title {
  color: rosybrown;
  font-size: 50px;
  filter: blur(1px);
  position: sticky;
  transition: all 0.3s;
  transform: translate(20px);
}
.title.active {
  color: palegoldenrod;
}